import React from 'react';
import {Link} from 'react-router-dom';
import './listing.css';

const ListingDisplay = (props) => {
    const renderList = ({restaurentList}) => {
        if(restaurentList){
            return restaurentList.map((item) => {
                return(
                    <div class="CC">
                        <div class="CC1">
                            <img src={item.thumb}/>
                        </div>
                    <div class="CC2">
                        <div class="Heading">
                            <h3><Link to={`/details/${item._id}`}>
                                {item.name}
                            </Link></h3>
                        </div>
                        <div class="SubHeading">
                            RS.{item.cost} Per two people
                            <hr/>
                            {item.locality}
                            <br/>
                            {item.city}
                        </div>
                    </div>
                </div>
                )
            })
        }else{
            return(
                <div className="load">
                    <img src="/images/loader.gif"/>
                </div>
            )
        }

    }
    return(
       <>
            {renderList(props)}
       </>
    )
}

export default ListingDisplay;