import React,{Component} from 'react';
import { withRouter } from 'react-router-dom';
import './Search.css';

const url = "https://naughtyard.herokuapp.com/city";
const rurl = "https://naughtyard.herokuapp.com/rest?city=";

class Search extends Component{

    constructor(){
        super()

        this.state={
            city:'',
            rest:''
        }
    }

    //Display City in option
    renderCity = (data) => {
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item.city_name}>{item.city_name} | {item.country_name}</option>
                )
            })
        }
    }

    renderRest = (data) => {
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item._id}>{item.name} | {item.locality}</option>
                )
            })
        }
    }

    handleCity=(event) => {
        console.log(event.target.value)
        const city = event.target.value;
        fetch(`${rurl}${city}`,{method:'GET'})
        .then((res) => res.json())
        .then((data)=> {
            this.setState({rest:data})
        })
    }

    handleRest=(event) => {
        this.props.history.push(`/details/${event.target.value}`)
    }
    
    render(){
        return(
            <React.Fragment>
                <div class="imageContainer">
                    <div style={{textAlign:'right'}}>

                        <a className="fb myfont" href="https://www.facebook.com/rknayakbabu" target="_blank">
                            <img src="/images/facebook.png" className="social_logo"/>
                        </a>
                        <a class="yt myfont" href="https://www.youtube.com/channel/UCAGf8EY29uVhpt9pm0NklnQ" target="_blank">
                            <img src="/images/youtube.png" className="social_logo"/>
                        </a>
                    </div>
                    <div id="logo">
                        <b>FZ</b>
                    </div>
                    <div id="heading">
                        Find Bést Restaurants, Cafés, bars
                    </div>
                    <div className="locationSelector">
                        <select class="dropdown" onChange={this.handleCity}>
                            <option>- - - - - -Select City- - - - -</option>
                            {this.renderCity(this.state.city)}
                        </select> 
                        <select className="dropdown1" onChange={this.handleRest}>
                        <option>- - - - - - -Select Restaurent- - - - - -</option>
                            {this.renderRest(this.state.rest)}
                            
                        </select>
                    </div>
                </div>
            </React.Fragment>
        )
    }

    componentDidMount(){
                
                fetch(url,{method:'GET'})
                .then((res) => res.json())
                .then((data)=> this.setState({city:data}))
            }
        }

export default withRouter(Search);